.cp-final .close-partner__img {
  animation-timing-function: ease;
}

.cp-final .close-partner__img_ipad {
  animation-duration: 7s;
  animation-iteration-count: infinite;
  animation-name: cp-anim-ipad-final;
}

.cp-final .close-partner__img_iphone {
  transform-origin: 100% 100%;
  animation-duration: 10s;
  animation-iteration-count: infinite;
  animation-name: cp-anim-iphone-final;
}

.cp-final .close-partner__img_airpods-l {
  animation-duration: 5s;
  animation-iteration-count: infinite;
  animation-name: cp-anim-airpods-l-final;
}

.cp-final .close-partner__img_airpods-r {
  animation-duration: 6s;
  animation-iteration-count: infinite;
  animation-name: cp-anim-airpods-r-final;
}

.close-partner-list {
